.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    /* stylelint-disable-next-line */
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 35px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

@keyframes transparency {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fromtop {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes fromleft {
  0% {
    transform: translateX(-100%);
    opacity: 0; }
  40% {
    transform: translateX(30px); }
  70% {
    transform: translateX(-10px); }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes frombottom {
  0% {
    transform: translateY(100%);
    opacity: 0; }
  40% {
    transform: translateY(-30px); }
  70% {
    transform: translateY(10px); }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fromleft-slide {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(0); } }

@keyframes move-icon-1 {
  40% {
    transform: translate(-5px, -18px); } }

@keyframes move-icon-2 {
  40% {
    transform: translate(-2px, -8px); } }

@keyframes move-icon-3 {
  40% {
    transform: translate(16px, -18px); } }

@keyframes move-icon-4 {
  40% {
    transform: translate(5px, -10px); } }

.header {
  background: #ffffff;
  z-index: 100; }
  @media (max-width: 1023px) {
    .header {
      margin-bottom: 0; } }

.header__wrapper {
  padding: 15px 25px; }
  @media (max-width: 767px) {
    .header__wrapper {
      padding: 15px 16px; } }

.header__logo {
  display: inline-block;
  vertical-align: baseline;
  padding: 10px;
  animation: transparency 1.5s ease;
  transition: 0.8s;
  position: relative;
  outline: none; }
  @media (min-width: 1439px) {
    .header__logo::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 52%;
      transform: translateX(-52%);
      width: 0;
      height: 2px;
      background: #2c39f2;
      transition: 0.4s; }
    .header__logo:hover .header__icon,
    .header__logo:focus .header__icon {
      transform: scale(1.1); }
    .header__logo:hover::after, .header__logo:focus::after {
      width: calc(100%); }
    .header__logo:active,
    .header__logo:active .header__icon {
      opacity: 0.5;
      transform: scale(1); } }

.header__icon {
  vertical-align: top;
  transition: 0.4s; }

.header__text {
  display: none; }

.header--loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center; }
  .header--loader .header__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .header--loader .header__logo {
    padding: 0;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    animation: fromtop 1.5s ease;
    pointer-events: none; }
    @media (max-width: 1023px) {
      .header--loader .header__logo {
        max-width: 550px; } }
  .header--loader .header__icon {
    width: 100%;
    height: auto; }
  .header--loader .header__text {
    display: block;
    text-align: center;
    width: 100%;
    font-size: 24px;
    line-height: 26px;
    position: absolute;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
    animation: transparency 4s ease; }
    @media (max-width: 767px) {
      .header--loader .header__text {
        font-size: 20px;
        line-height: 22px; } }

.main {
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative; }
  .main::after {
    content: "";
    position: fixed;
    bottom: 0;
    width: 100%;
    height: calc(100vh - 110px);
    background: url("../img/bg/bg-a-letter.svg") no-repeat center top/contain;
    z-index: -1; }
    @media (max-width: 1023px) {
      .main::after {
        display: none; } }

.intro {
